<template>
	<view>
		<view class="content-head" :style="{ padding: top + 'px 0 10px 0' }">个人设置</view>
		<progress :percent="percent" strock-width="10"></progress>
		<view class="head-tx" style="height:80px;line-height:80px;">
			<view>头像</view>
			<view class="head-acc" style="height:80px;line-height:80px;">
				<button type="primary" @tap="uploadTx" style="line-height:78px;background:#FFFFFF;border-radius:50%;">
					<image :src="userInfo.avatar" mode="aspectFill" class="head-img" style="margin-bottom:-24px;border-radius:50%;">
				</button>
			</view>
		</view>
		<!-- <view class="head-tx">
			<view>用户昵称</view>
			<view class="head-acc" @click="editUserInfo(1)">{{userInfo.nickname}} <image src="../../static/imgs/r.png" style="width:12px;height:10px;"></image></view>
		</view> -->
		<view class="head-tx">
			<view>绑定手机</view>
			<view class="head-acc" v-if="isTel">{{userInfo.tel}}</view>
			<view class="head-acc" @click="editUserInfo(2)" v-else>绑定后不可修改 <image src="../../static/imgs/r.png" style="width:12px;height:10px;"></image></view>
		</view>
		<view class="head-tx">
			<view>登录账号</view>
			<view class="head-acc">{{userInfo.username}}</view>
		</view>
		<view class="head-tx">
			<view>修改密码</view>
			<view class="head-acc" @click="editUserInfo(3)">修改登录密码<image src="../../static/imgs/r.png" style="width:12px;height:10px;"></image></view>
		</view>
		<neil-modal :show="isNotice" @confirm="confirm" @close="closeNotice" :title="title" confirm-text="提交数据" cancel-text="我再想想">
		    <view style="min-height:90upx;padding:24upx 30upx 10upx;"> 
			<view v-if="type==1"><input class="input" type="text" :value="value" maxlength="15" @input="inputUserInfo" placeholder="请输入用户昵称"/></view>
			<view v-if="type==2"><input class="input" type="number" :value="value" maxlength="11" @input="inputUserInfo" placeholder="请输入手机号"/></view>
			<view v-if="type==3">
				<input class="input pwd" type="password" :value="fromData.oldPwd" id="oldPwd" maxlength="15" @input="inputPwd" placeholder="请输入原密码"/>
				<input class="input pwd" type="password" :value="fromData.newPwd" id="newPwd" maxlength="15" @input="inputPwd" placeholder="请输入新密码"/>
				<input class="input pwd" type="password" :value="fromData.rPwd" id="rPwd" maxlength="15" @input="inputPwd" placeholder="再输入一次"/>
			</view>
		    </view>
		</neil-modal>
	</view>
</template>

<script>
import neilModal from '@/components/neil-modal/neil-modal.vue';
import api from "@/common/api.js";
var _self;
export default {
	data() {
		return {
			userInfo: {},
			userId: 0,
			isNotice: false,
			isTel: false,
			title: '提示',
			value: 123,
			type: 1,
			percent: 0,
			imgs: '',
			fromData: {
				oldPwd: '',
				newPwd: '',
				rPwd: ''
			},
			top: 27,
		}
	},
	onLoad() {
		_self = this;
		uni.showLoading({ title:'加载中...',mask:true });
		var mobile = uni.getSystemInfoSync();
		_self.top = mobile.safeArea.top+7;
		// 是否登录
		var info = api.getLogins();
		if(info === false) {
			_self.skipUrl();
		}else{
			_self.userId = info.userId;
			_self.getUserInfo();
		}
		//uni.hideLoading();
	},
	methods: {
		// 读取个人信息
		getUserInfo() {
			uni.request({
				url: api.apiData.getInfo,
				method: 'POST',
				data: {
					userId: _self.userId
				},
				header: {
					'Content-type': 'application/x-www-form-urlencoded'
				},
				success: (e) => {
					//console.log(e)
					_self.userInfo = e.data.Data;
					_self.userInfo.avatar = (_self.userInfo.avatar == '') ? '/static/imgs/tx.jpg' : _self.userInfo.avatar;
					if(_self.userInfo.tel!=null&&_self.userInfo.tel.length>0) _self.isTel = true;
					//console.log(_self.userInfo.avatar)
					uni.hideLoading();
				},
				fail: () => {
					// 加载失败提示
					_self.msgData('服务器连接失败，请检查网络是否正常', 5000);
				}
			});
		},
		// 弹出修改框
		editUserInfo(e) {
			uni.showLoading({ title:'加载中...',mask:true });
			_self.isNotice = true;
			if(e == 1){
				_self.title = "请输入新昵称";
				_self.value = _self.userInfo.nickname;
				_self.type = 1;
			}else if(e == 2){
				_self.title = "请输入新手机号";
				_self.value = _self.userInfo.tel;
				_self.type = 2;
			}else{
				_self.title = "修改登录密码";
				_self.fromData.oldPwd = '';
				_self.fromData.newPwd = '';
				_self.fromData.rPwd = '';
				_self.type = 3;
			}
			uni.hideLoading();
		},
		// 修改密码
		inputPwd(e) {
			_self.fromData[e.target.id] = e.target.value;
		},
		// 获取用户输入的信息
		inputUserInfo(e) {
			_self.value = e.detail.value;
		},
		// 提交保存
		confirm(type = 0) {
			_self.msgData("处理中...");
			var content = (type == 1) ? _self.imgs : _self.value;
			if(_self.type == 3){
				var postUrl = api.apiData.changePwd;
				var data = {
					userId: _self.userId,
					oldPwd: _self.fromData.oldPwd,
					newPwd: _self.fromData.newPwd,
					confirm: _self.fromData.rPwd
				}
			}else{
				var postUrl = api.apiData.editInfo;
				var data = {
					type: _self.type,
					userId: _self.userId,
					content: content
				}
			}
			//console.log(data)
			uni.request({
				url: postUrl,
				method: 'POST',
				data: data,
				header: {
					'Content-type': 'application/x-www-form-urlencoded'
				},
				success: (e) => {
					if(type == 0) _self.msgData(e.data.Msg);
					if(e.data.Code == 200){
						if(_self.type == 1){
							_self.userInfo.nickname = _self.value;
						}else if(_self.type == 2){
							_self.userInfo.tel = _self.value;
							_self.isTel = true;
						}else if(_self.type == 3){
							api.delLogins();
							_self.skipUrl();
							_self.msgData("密码修改成功");
						}else{
							_self.percent = 0;
							_self.msgData("头像更换成功");
						}
					}
				},
				fail: () => {
					_self.msgData("服务器连接失败，请检测网格是否正常");
				}
			});
		},
		// 取消
		closeNotice() {
			_self.isNotice = false;
		},
		// 上传头像
		uploadTx(){
			uni.chooseImage({
				count: 1,
				sizeType:['copressed'],
				success(res) {
					//因为有一张图片， 输出下标[0]， 直接输出地址
					var imgFiles = res.tempFilePaths[0]
					//console.log(imgFiles)
					// 上传图片
					// 做成一个上传对象
					var uper = uni.uploadFile({
						// 需要上传的地址
						url: api.apiData.upload,
						// filePath  需要上传的文件
						filePath: imgFiles,
						//fileType: 'image',
						name: 'fileName',
						formData: {
							fileType: 'image'
						},
						success(e) {
							var d = eval("(" + e.data + ")");
							// 显示上传信息
							if(d.Code == 200){
								_self.userInfo.avatar = d.Data;
								//console.log(_self.userInfo.avatar)
								_self.type = 0;
								_self.imgs = d.Data;
								_self.confirm(1);
							}else{
								_self.msgData("服务器连接失败，请检测网格是否正常", 2000);
							}
							//console.log(d)
						}
					});
					// onProgressUpdate 上传对象更新的方法
					uper.onProgressUpdate(function(res){
						// 进度条等于 上传到的进度
						_self.percent = res.progress
						//console.log('上传进度' + res.progress)
						//console.log('已经上传的数据长度' + res.totalBytesSent)
						//console.log('预期需要上传的数据总长度' + res.totalBytesExpectedToSend)
					})
				}
			})
		},
		// 跳转登录
		skipUrl() {
			uni.reLaunch({ url:"/pages/login/login" });
		},
		// 提示
		msgData(title = '', times = 1000, style = 'none') {
			// 加载失败提示
			uni.showToast({
				icon: style,
				title: title,
				mask: true,
				duration: times
			});
		}
	},
	// 下拉刷新
	onPullDownRefresh() {
		_self.getUserInfo();
		uni.stopPullDownRefresh();
	},
	components: { neilModal }
}
</script>

<style>
.content-head{ 
	//position: fixed;
	width: 100%;
	height: 30px;
	line-height: 30px;
	//top: 27px;
	text-align: center;
	//color: #666666; 
	background: #f8f8f8;
	font-size: 17px;
	border-bottom: 1px solid #f7f7f7;
}
.input{
	width: 96%;
	padding: 5px 2%;
	border: 1px solid #E2E2E2;
	border-radius: 5px;
}
.pwd{ margin-bottom:10px; }
.head-tx view{
	display: inline-block;
}
.head-tx{
	width: 90%;
	height: 60px;
	line-height: 60px;
	color: #000000;
	margin-top: 15px;
	font-size: 30upx;
	margin: auto;
	border-bottom: 1upx solid #E2E2E2;
}
.head-img{
	width: 60px;
	height: 60px;
}
.head-acc{
	float: right;
	color: #999999;
}
</style>
